.clock {
    /* font-family: 'Fjalla One', sans-serif; */
    font-family: 'Anton', sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    width: 700px;
    height: 200px;
    border-radius: 30px;

    background-color: rgba(0, 0, 0, 0.2);
    color: white;

    display: flex;
    flex-direction: row;
    text-align: center;

    font-size: 120px;
}

